body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background: linear-gradient(135deg, #e0e0e0, #f7f7f7); /* Subtle gradient background */
  font-family: 'Arial', sans-serif;
}

.game {
  text-align: center;
  background: #ffffff;
  border: 2px solid #333;
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15); /* Softer shadow */
  padding: 30px;
  max-width: 400px; /* Restrict width for better centering */
  width: 100%;
  margin: 20px;
  transition: transform 0.3s ease; /* Add smooth transitions */
}

.game:hover {
  transform: translateY(-5px); /* Slight lift on hover */
}

.status {
  font-size: 26px;
  margin-bottom: 25px;
  font-weight: bold;
  color: #333; /* Darker text color for better contrast */
}

.board {
  display: inline-block;
}

.board-row {
  display: flex;
  justify-content: center;
}

.square {
  width: 70px; /* Slightly larger squares */
  height: 70px;
  font-size: 26px;
  font-weight: bold;
  color: #fff;
  background-color:#b696e0; /* Green background */
  border: none;
  border-radius: 10px; /* More rounded corners */
  margin: 6px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s, box-shadow 0.2s;
}

.square:hover {
  background-color: #a274df; /* Darker green on hover */
  transform: scale(1.08); /* Slightly larger hover effect */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1); /* Add shadow effect on hover */
}

.square:focus {
  outline: none; /* Remove outline on focus */
}

.reset {
  margin-top: 25px;
  padding: 12px 25px;
  font-size: 18px; /* Larger button text */
  font-weight: bold;
  cursor: pointer;
  background-color: #ff5722; /* Red background */
  color: #fff;
  border: none;
  border-radius: 7px; /* Slightly larger rounding */
  transition: background-color 0.3s, transform 0.2s, box-shadow 0.2s;
}

.reset:hover {
  background-color: #e64a19; /* Darker red on hover */
  transform: translateY(-3px); /* Small lift effect */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1); /* Add shadow effect on hover */
}
